	width: 100%;
	height: 6px;
	background-color: #fff;
	border-radius: 16px;
	position: absolute;
	-webkit-transform-origin: center;
	        transform-origin: center;
	z-index: 3;
}

.line.first {
	top: 0;
	margin-top: 0;
	-webkit-transform: rotate(0);
	        transform: rotate(0);
	transition: margin 0.3s 0.3s, background 0.5s, -webkit-transform 0.3s;
	transition: transform 0.3s, margin 0.3s 0.3s, background 0.5s;
	transition: transform 0.3s, margin 0.3s 0.3s, background 0.5s, -webkit-transform 0.3s;
}

.line.second {
	top: 50%;
	-webkit-transform: scaleX(1) translateY(-4px);
	        transform: scaleX(1) translateY(-4px);
	transition: background 0.5s, -webkit-transform 0.3s 0.3s;
	transition: transform 0.3s 0.3s, background 0.5s;
	transition: transform 0.3s 0.3s, background 0.5s, -webkit-transform 0.3s 0.3s;
}

.line.third {
	bottom: 0;
	margin-bottom: 0;
	-webkit-transform: rotate(0);
	        transform: rotate(0);
	transition: margin 0.3s 0.3s, background 0.5s, -webkit-transform 0.3s;
	transition: transform 0.3s, margin 0.3s 0.3s, background 0.5s;
	transition: transform 0.3s, margin 0.3s 0.3s, background 0.5s, -webkit-transform 0.3s;
}

#toggleNav:checked ~ .line {
	background-color: #000;
	transition: all 1s;
}

#toggleNav:checked ~ .line.second {
	-webkit-transform: scaleX(0) translateY(-2px);
	        transform: scaleX(0) translateY(-2px);
	transition: 0.2s;
}

#toggleNav:checked ~ .line.first {
	margin-top: 12px;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	transition: margin 0.3s, -webkit-transform 0.3s 0.3s;
	transition: margin 0.3s, transform 0.3s 0.3s;
	transition: margin 0.3s, transform 0.3s 0.3s, -webkit-transform 0.3s 0.3s;
}

#toggleNav:checked ~ .line.third {
	margin-bottom: 12px;
	-webkit-transform: rotate(135deg);
	        transform: rotate(135deg);
	transition: margin 0.3s, -webkit-transform 0.3s 0.3s;
	transition: margin 0.3s, transform 0.3s 0.3s;
	transition: margin 0.3s, transform 0.3s 0.3s, -webkit-transform 0.3s 0.3s;
}

.navbar-mobile-list {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 1;
    transition: all 0.3s;
}

#toggleNav:checked ~ .navbar-mobile-list {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
    transition: all 0.3s;
}

#toggleNav:checked ~ .overlay {
	display: block;
}
